/* eslint-disable no-shadow */
import React, { cloneElement } from 'react';
import { PropTypes, describe } from 'react-desc';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import DSBreadcrumbItem from './DSBreadcrumbItem';

const blockName = 'breadcrumb';

const DSBreadcrumb = ({
  containerProps = {},
  as: Component = 'nav',
  label = 'Breadcrumb',
  isTitle = false,
  children,
  ...rest
}) => {
  const SafeComponent =
    ['nav', 'div', 'header', 'footer', 'main'].indexOf(Component) > -1
      ? Component
      : 'nav';
  const NavContainer = aggregatedClasses(SafeComponent)(blockName);

  return (
    <NavContainer
      aria-label={label}
      data-testid="breadcrumb-nav-container"
      {...rest}
      {...containerProps}
    >
      <ol data-testid="list-container">
        {React.Children.map(children, (child, i) =>
          i < children.length - 1
            ? child
            : cloneElement(child, {
                active: true,
                isTitle,
              }),
        )}
      </ol>
    </NavContainer>
  );
};

DSBreadcrumb.Item = DSBreadcrumbItem;

const props = {
  /** props injected to breadcrumb wrapper */
  containerProps: PropTypes.object.description(
    'props injected to breadcrumb wrapper',
  ),
  /**
   * Type of element you want the main container to be:
   * 'nav', 'div', 'header', 'footer', 'main'
   */
  as: PropTypes.string.description(
    "Type of element you want the main container to be: 'nav', 'div', 'header', 'footer', 'main'",
  ),
  /**
   * Whether to show the last element of the breadcrumb as a title
   * Also removes trailing slash to show last element as title
   */
  isTitle: PropTypes.bool.description(
    'Whether to show the last element of the breadcrumb as a title. Also removes trailing slash to show last element as title',
  ),
  /** breadcrumb label */
  label: PropTypes.string.description('breadcrumb label'),
  /**
   * Container items to wrap
   */
  children: PropTypes.arrayOf(DSBreadcrumbItem).isRequired.description(
    'Container items to wrap',
  ),
};

DSBreadcrumb.propTypes = props;

const DSBreadcrumbWithSchema = describe(DSBreadcrumb);
DSBreadcrumbWithSchema.propTypes = props;

export { DSBreadcrumb, DSBreadcrumbWithSchema };
export default DSBreadcrumb;
