/* eslint-disable react/prop-types */
/* eslint-disable indent */
/* eslint-disable import/extensions,react/no-unused-state,no-shadow,max-lines */
import React, { Component } from 'react';
import { compose, arrayMove } from '@elliemae/ds-utilities/utils';
import withProviders from './withProviders';
import {
  addToTarget,
  navigateTo,
  resetCheckedItems,
  resetMovedItems,
  toggleItemSelection,
  removeFromTarget,
} from './Shuttle.actions';
import {
  getActiveIdFromHierarchy,
  filterBySearch,
  getItemsById,
  pipe,
  getChildrenFromParent,
  filterMovedItems,
} from './helper';
import ShuttleRenderer from './ShuttleRenderer';
import SearchState from './SearchState';
import updateShuttleStateFromProps from './updateShuttleStateFromProps';

class ShuttleImpl extends Component {
  static contextType = SearchState.Context;

  constructor(props) {
    super(props);
    this.state = {
      prevProps: {},
      preparedItems: [],
      checkedItems: [],
      // left breadcrumb
      hierarchy: [{ name: props.sourceRootTitle, id: null }],
      // right breadcrumb
      hierarchyDest: [{ name: props.targetRootTitle, id: null }],

      movedItems: [],
      sourceItems: [],
      targetItems: [],
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    return updateShuttleStateFromProps(nextProps, prevState);
  }

  componentDidMount() {
    const { setGetStatus } = this.props;
    setGetStatus(this.getState);
  }

  getState = () => {
    const { movedItems } = this.state;
    return Object.keys(movedItems);
  };

  handleChange() {
    const { onChange } = this.props;
    const { movedItems } = this.state;
    onChange(Object.keys(movedItems));
  }

  navigate(item, dest, direction) {
    const { onDrillDown, onDrillDownTarget } = this.props;

    if (!dest) onDrillDown(item, direction);
    if (dest) onDrillDownTarget(item, direction);

    const { reset: resetSearchState } = this.context;
    resetSearchState();

    this.setState(pipe([navigateTo(item, dest), resetCheckedItems()]));
  }

  resetCheckedItems() {
    this.setState(resetCheckedItems());
  }

  resetMovedItems() {
    const { selectedItems, onRemoveAllFromTarget } = this.props;
    if (!selectedItems) {
      this.setState(
        pipe([navigateTo({ id: null }, true), resetMovedItems()]),
        () => {
          this.handleChange();
        },
      );
    } else {
      this.setState(pipe([navigateTo({ id: null }, true)]));
    }
    onRemoveAllFromTarget();
  }

  addToTarget(item) {
    const { selectedItems, onAddToTarget } = this.props;
    if (!selectedItems) {
      this.setState(pipe([addToTarget(item), resetCheckedItems()]), () => {
        this.handleChange();
      });
    }
    onAddToTarget(item);
  }

  removeFromTarget(item) {
    const { selectedItems, onRemoveFromTarget } = this.props;
    if (!selectedItems) {
      this.setState(pipe([removeFromTarget(item)]), () => {
        this.handleChange();
      });
    }
    onRemoveFromTarget(item);
  }

  toggleItemSelection(item) {
    this.setState(toggleItemSelection(item.id));
  }

  moveCheckedItems() {
    const { onAddCheckedItems, selectedItems, nonSelectedItems } = this.props;
    const { checkedItems, preparedItems } = this.state;
    const isControlledV2 = !!selectedItems && !!nonSelectedItems;
    const checked = isControlledV2
      ? nonSelectedItems.filter((item) =>
          checkedItems.some((check) => check === item),
        )
      : checkedItems;
    const items = getItemsById(checked, preparedItems);
    const actions = !selectedItems
      ? items.map((item) => addToTarget(item))
      : [];
    this.setState(pipe([...actions, resetCheckedItems()]), () => {
      this.handleChange();
    });
    onAddCheckedItems(items);
  }

  handleTargetSortEnd({ sourceItem, targetItem }) {
    const { idField, onTargetSortEnd } = this.props;
    const { targetItems, hierarchyDest, movedItems } = this.state;

    const hierarchyId = getActiveIdFromHierarchy(hierarchyDest).id;

    const collection = !hierarchyId ? movedItems : targetItems;

    const oldIndex = collection.findIndex(
      (tItem) => tItem[idField] === sourceItem[idField],
    );
    const newIndex = collection.findIndex(
      (tItem) => tItem[idField] === targetItem[idField],
    );

    const sortedMovedItems = !hierarchyId
      ? arrayMove(movedItems, oldIndex, newIndex)
      : movedItems;
    const sortedItems = hierarchyId
      ? arrayMove(targetItems, oldIndex, newIndex)
      : targetItems;

    this.setState({
      targetItems: sortedItems,
      movedItems: sortedMovedItems,
    });
    onTargetSortEnd({ sourceItem, targetItem });
  }

  render() {
    const { idField, nonSelectedItems, moreItemsLoading, ...rest } = this.props;
    const {
      hierarchy,
      hierarchyDest,
      checkedItems,
      movedItems,
      preparedItems: items,
      targetItems,
    } = this.state;
    const {
      state: { searchTerm, searching },
    } = this.context;

    const handleControlledSourceItms = () => {
      const { id } = getActiveIdFromHierarchy(hierarchy);
      if (id) {
        return filterBySearch(
          searchTerm,
          searching,
        )(
          getChildrenFromParent(id)(
            nonSelectedItems.map((it) => items.find((itt) => itt.id === it)),
          ),
        );
      }

      return filterBySearch(
        searchTerm,
        searching,
      )(
        items.filter((it) =>
          nonSelectedItems.some(
            (itt) =>
              itt === it.id &&
              !nonSelectedItems.some((ittt) => ittt === it.parent),
          ),
        ),
      );
    };

    let composedSourceItems;

    // controlled V2 TODO make it default controlled behavior
    if (nonSelectedItems) {
      composedSourceItems = handleControlledSourceItms();
    } else {
      composedSourceItems = compose(
        filterBySearch(searchTerm, searching),
        filterMovedItems(movedItems),
        getChildrenFromParent(getActiveIdFromHierarchy(hierarchy).id),
      )(items);
    }

    const handleControlledTargetItms = () => {
      const { id } = getActiveIdFromHierarchy(hierarchyDest);
      if (id) {
        return getChildrenFromParent(
          getActiveIdFromHierarchy(hierarchyDest).id,
        )(
          this.props.selectedItems.map((it) =>
            items.find((itt) => itt.id === it),
          ),
        );
      }

      return targetItems.filter((it) =>
        this.props.selectedItems.some(
          (itt) =>
            itt === it.id &&
            !this.props.selectedItems.some((ittt) => ittt === it.parent),
        ),
      );
    };

    let composedTargetItems;

    // controlled V2 TODO make it default controlled behavior
    if (nonSelectedItems) {
      composedTargetItems = handleControlledTargetItms();
    } else {
      composedTargetItems = compose((tItems) => {
        const hierarchyId = getActiveIdFromHierarchy(hierarchyDest).id;
        return hierarchyId
          ? compose(
              filterMovedItems(movedItems),
              getChildrenFromParent(hierarchyId),
            )(tItems)
          : movedItems;
      })(targetItems);
    }

    return (
      <ShuttleRenderer
        {...rest}
        moreItemsLoading={moreItemsLoading}
        checkedItems={checkedItems}
        hierarchy={hierarchy}
        hierarchyDest={hierarchyDest}
        onCheckItem={this.toggleItemSelection}
        onClearCheckedItems={this.resetCheckedItems}
        onClearMovedItems={this.resetMovedItems}
        onItemCheck={this.toggleItemSelection}
        onMoveCheckedItems={this.moveCheckedItems}
        onMoveItemToSource={(item) => this.removeFromTarget(item, false)}
        onMoveItemToTarget={(item) => this.addToTarget(item, true)}
        onNavigateOnSourceTo={(item, direction) =>
          this.navigate(item, false, direction)
        }
        onNavigateOnTargetTo={(item, direction) =>
          this.navigate(item, true, direction)
        }
        onSourceSortEnd={() => null}
        onTargetSortEnd={this.handleTargetSortEnd}
        sourceItems={composedSourceItems}
        targetItems={composedTargetItems}
      />
    );
  }
}

export default withProviders(ShuttleImpl);
