/* eslint-disable no-underscore-dangle */
import React, { useState, useCallback, useEffect } from 'react';
import { isMovable } from '../../helper';
import { DrillDownButton, MoveButton } from './ActionButtons';
import ShuttleListItem from './ShuttleListItem';

function ShuttleSourceListItem({
  item = {},
  onItemCheck = () => null,
  onMoveToTarget = () => null,
  onNavigateOnSourceTo = () => null,
  isChecked = false,
  showMoveButton = true,
  showDrillDownButton = true,
  index,
  ...rest
}) {
  const [clicked, setClicked] = useState(false);
  const moveToTarget = useCallback(() => onMoveToTarget(item), [item]);
  useEffect(() => {
    if (clicked) {
      moveToTarget();
    }
  }, [clicked]);
  const navigateOnSourceTo = useCallback(() => onNavigateOnSourceTo(item), [
    item,
  ]);
  const checkItem = useCallback(() => {
    if (!clicked) onItemCheck(item);
  }, [item, clicked]);
  const handleOnClick = () => {
    if (!clicked) {
      setClicked(true);
    }
  };

  return (
    <ShuttleListItem
      {...rest}
      actions={[
        showDrillDownButton && (
          <DrillDownButton
            key="drill-down"
            disabled={
              (!item.hasChildren && !item.asyncLoad) ||
              item.readOnly ||
              item.disableDrillDown
            }
            onClick={navigateOnSourceTo}
          />
        ),
        showMoveButton && (
          <MoveButton
            data-testid="ds-shuttle__move-button"
            key="move"
            disabled={!isMovable(item) || item.readOnly}
            onClick={handleOnClick}
          />
        ),
      ]}
      isChecked={isChecked}
      item={item}
      onSelect={checkItem}
    />
  );
}

export default ShuttleSourceListItem;
