/* eslint-disable no-shadow,no-plusplus */
import { omit, compose, hashArray } from '@elliemae/ds-utilities/utils';
import * as tree from 'treetabular';

export const getHierarchyTypeName = (dest) =>
  !dest ? 'hierarchy' : 'hierarchyDest';
export const checkHasChildren = (list, currentItem) =>
  list.some((item) => currentItem.id === item.parent);
export const getActiveIdFromHierarchy = (hierarchy = []) =>
  hierarchy[hierarchy.length - 1] || {};

export const filterItemsByHierarchy = (
  list,
  hierarchyId,
  parentField = 'parent',
) => list.filter((item) => item[parentField] === hierarchyId);
export const getItemsById = (ids, list) =>
  ids
    .reduce(
      (acc, id) =>
        acc.concat(list.find((item) => String(item.id) === String(id))),
      [],
    )
    .filter((item) => item);
export const toggleInObject = (obj, key, value) =>
  obj[key] ? omit(obj, [key]) : { ...obj, [key]: value };
export const isMovable = (item) =>
  !item.forceNotMovable &&
  ((item.hasChildren && item.isMoveableContainer) || !item.hasChildren);
// utility to execute all specified actions in sequence
export const pipe = (actions) => (initState, props) =>
  actions.reduce((prevState, action) => action(prevState, props), initState);

export const moveItems = ({ oldIndex, newIndex }) => (items) => {
  const sourceItem = items[oldIndex];
  items.splice(oldIndex, 1);
  items.splice(newIndex, 0, sourceItem);
  return items;
};

export const prepareItems = (items) =>
  compose(tree.fixOrder(), (items) =>
    items.map((item) => ({
      ...item,
      hasChildren: checkHasChildren(items, item),
    })),
  )(items);

const defaultOptions = {
  parentField: 'parent',
  idField: 'id',
  op: (hasMoved) => !hasMoved,
};

export function getChildrenFromParent(parentId, options) {
  const { parentField } = { ...defaultOptions, ...options };
  return (items) => items.filter((item) => item[parentField] === parentId);
}

export function filterMovedItems(movedItems, options) {
  const { idField, op: filterOp } = { ...defaultOptions, ...options };
  const movedItemsHash = hashArray(movedItems, idField);
  return (items) =>
    items.filter((item) => filterOp(!!movedItemsHash[item[idField]]));
}

export function filterBySearch(term, searching) {
  return (items) =>
    searching
      ? items.filter(
          (item) =>
            item.name.toLowerCase().indexOf(String(term).toLowerCase()) !== -1,
        )
      : items;
}
